
import { defineComponent, reactive, ref, toRefs } from "vue";

export default defineComponent({
  name: "MyName",
  setup() {
    type Person = {
      name?: string;
      age: number;
    };

    const name = reactive<Person>({
      name: "张三",
      age: 18
    });

    const mySayStr = ref<string>("我是谢大脚!!!");
    const mtName = ref<number>(23);
    return {
      mySayStr,
      mtName,
      ...toRefs(name)
    };
  }
});
